<template>
    <div class="row">
        <div class="col-md-12">
            <element-box>
                <form @submit.prevent="subscribe">
                    <div v-html="html"></div>
                    <input type="submit" value="enviar">
                </form>
            </element-box>
        </div>
    </div>
</template>
<script>

    require('formBuilder/dist/form-render.min');
    import programsMixins from '../../../../mixins/programs'

    export default {
        data() {
            return {
                html: ''
            }
        },
        mixins: [programsMixins],
        components: {},
        mounted() {
            if (this.profileCanSubscribe()) {
                this.loadProgram();
            } else {
                this.denySubscripition();
            }
        },
        methods: {
            renderForm() {
                let formRenderOpts = {
                    dataType: 'json',
                    formData: this.program.form_custom
                };
                var renderedForm = $('<div>');
                renderedForm.formRender(formRenderOpts);
                this.html = renderedForm.html();
            },
            subscribe(e) {
                if (this.profileCanSubscribe()) {
                    this.preload(true);
                    axios.post(this.$store.state.CONSTANTS.urls.SUBSCRIBE_PROGRAM_URL(this.$route.params.id), {
                        Authorization: `Bearer ${this.$store.state.auth.token}`,
                        'Content-Type': 'multipart/form-data'
                    }, new FormData(e.target)).then(() => {
                        this.$snotify.success('Inscrio realizada', 'Sucesso');
                        this.$router.push({name:'admin.program.index'})
                    }).catch((e) => {
                        this.$snotify.error('Erro', e.response.data.errors[0]);
                    }).finally(() => {
                        this.preload(false)
                    });
                } else {
                    this.denySubscripition();
                }
            },
            loadProgram() {
                this.preload(true);
                this.getProgram({id: this.$route.params.id}).then(() => {
                    this.preload(false);
                    this.renderForm();
                });
            },
            profileCanSubscribe() {
                return this.$store.state.auth.company.profile === 2;
            },
            denySubscripition() {
                this.$router.push({name: 'admin.index'});
                this.$snotify.error('Empresas Proponentes no podem se cadastrar em programas', 'Erro');
            }
        }
    }
</script>